html {
    margin: 0;
    width: 100%;
    height: 100%;
}

body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    background: #121212;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
